import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/components/Index'

// 下面组件没有指定webpackChunkName，每个组件打包成一个js文件，按需加载
const RESTClient = () => import('@/components/RESTClient')
const JsonEditor = () => import('@/components/JsonEditor')
const QRCode = () => import('@/components/QRCode')
const ColorConversion = () => import('@/components/ColorConversion')
const Length = () => import('@/components/Length')

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Index',
      component: Index
    },
    {
      path: '/restclient',
      name: 'RESTClient',
      component: RESTClient
    },
    {
      path: '/JsonEditor',
      name: 'JsonEditor',
      component: JsonEditor
    },
    {
      path: '/QRCode',
      name: 'QRCode',
      component: QRCode
    },
    {
      path: '/ColorConversion',
      name: 'ColorConversion',
      component: ColorConversion
    },
    {
      path: '/Length',
      name: 'Length',
      component: Length
    }
  ]
})



